@charset 'UTF-8';
@import 'variables';
@import '../../../sass/func';

p,
h1,
h5,
h6,
body {
  margin: 0;
}

.protocol {
  margin-bottom: 18px;
  padding: 0 15px;

  font-size: 13px;
  line-height: 26px;
  text-align: justify;

  color: #151515;
}

.protocol {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  strong {
    font-weight: bold;

    color: #333;
  }
  section {
    padding-bottom: 24px;
  }
  blockquote {
    padding-bottom: 8px;
    margin: 0;
  }
  h1,
  .h1 {
    padding: 20px 0 5px;

    font-size: 16px;
  }
  h4,
  .h4 {
    font-size: 14px;
  }
  h5,
  .h5 {
    font-size: 13px;
  }

  h6,
  .h6 {
    padding-bottom: 14px;

    font-size: 12px;
  }
}


.indent {
  text-indent: 2em;
}

.right {
  text-align: right;
}

.underline {
  text-decoration: underline;
}

